import * as React from "react";
import type { HeadFC } from "gatsby";
import Layout from "../components/layout/Layout";
import { navigate, graphql } from "gatsby";
import Card from "react-bootstrap/Card";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Button from "react-bootstrap/Button";
import ListGroup from "react-bootstrap/ListGroup";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { WrappedParse as Parse } from "../vendor/ParseApp";
import HowItWorks from '../components/section/HowItWorks'


const IndexPage = () => {
  const { t } = useTranslation();
  const user = Parse?.User?.current();
  const isAuthenticated = !!user;

  return (
    <Layout>
      <HowItWorks />
      {

      
      "" && <Container className="color-white mt-5">
        <Row>
          <Col xs={12} md={3}></Col>
          <Col className="text-center" onClick={() => {}}>
            <Card className="color-brand-1">
              <Card.Header>{t("headline")}</Card.Header>

              <Card.Body>
                {t("bodyline")}
                <br />
                <br />
                <div>{t("helpline")}</div>
              </Card.Body>
              <ListGroup variant="list-group-flush">
                <ListGroup.Item>
                  {" "}
                  {!isAuthenticated && (
                    <Button onClick={() => navigate("/signup")} as="div">
                      Start Now
                    </Button>
                  )}
                  {isAuthenticated && (
                    <Button onClick={() => navigate("/start")} as="div">
                      Start Now
                    </Button>
                  )}
                </ListGroup.Item>
              </ListGroup>
            </Card>
          </Col>
          <Col xs={12} md={3}></Col>
        </Row>
      </Container>
      }
    </Layout>
  );
};

export default IndexPage;

export const Head: HeadFC = () => <title>Home Page</title>;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
